
#contact {
    text-align: center;
    height: 60vh;
}
.contact_container{
    width: 33rem;
}

.contact_option {
    padding: 1.2rem;
    border: 1px solid transparent;
}

.contact_header {
    font-size: 1.5rem;
}

input {
    background-color: #1C2025;
    border: 1px solid black;
    width: 80%;
    text-align: center;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-background-clip: text;
    -webkit-text-fill-color: white;
    /* transition: background-color 5000s ease-in-out 0s; */
    /* box-shadow: inset 0 0 20px 20px #23232329; */
}


input.top { 
    border-radius: 1rem 1rem 0 0 ; 
}

input.bottom {
    border-radius: 0 0 1rem 1rem ;
}

input, textarea {
    font-size: 1rem;
    padding: 1.5rem;
}

textarea {
    text-align: left;
    margin: 1rem;
    width: 80%;
    border-radius: 1rem;
    background-color: #1C2025;
    color: white;
    height: 11rem;
}

textarea::placeholder {
    text-align: center
}


button {
    align-items: center;
    background-image: linear-gradient(144deg,#300194, #82037e 50%,#9c3502);
    border: 0;
    border-radius: 8px;
    box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
    color: #FFFFFF;
    font-family: Phantomsans, sans-serif;
    font-size: 1rem;
    justify-content: center;
    line-height: 3.8rem;
    max-width: 100%;
    /* min-width: 140px; */
    padding: 3px;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    cursor: pointer;
}
.btn-container {
    display: flex;
    justify-content: center;
    gap: 3rem; 
}

      
.btn:active,
.btn:hover {
    outline: 0;
}
      
.btn span {
    background-color: rgb(5, 6, 45);
    padding: 17px 30px;
    border-radius: 6px;
    width: 100%;
    height: 100%;
    transition: 300ms;
}
      
.btn:hover span {
background: none;
}

  .fade-out {
    opacity: 1;
    transition: opacity 2s ease-out; 
  }
  .fade-out:hover {
    opacity: 0;
  }
  

/* ---------------- medium sized screens --------------------------*/
@media screen and (max-width: 1024px){
  
}

     
   
   
 /* ---------------- small sized screens --------------------------*/
   
   @media screen and (max-width: 600px){
    #contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: -6rem;
    }
    .contact_container{
        width: 100%;
    }

    input, textarea {
        text-align: center;
        padding: 1rem;
    }
    textarea {
        margin: 1rem;
        border-radius: 1rem;
        background-color: #1C2025;
        color: white;
        height: 8rem;
    }
    button {
        line-height: 2.5rem;
        font-size: .9rem;
        padding: 3px;
    }
    .btn-container {
        gap: 1rem; 
    }
    .btn span {
        /* width: 10rem; */
        /* height: 10rem; */
        transition: 300ms;
        padding: 8px 8px;
        border-radius: 6px;
    }

}